import React from 'react'

function Sesh(props) {

    return (
        <div className="flex-third">

            <h3 id="session-label">Session Length</h3>
            <p class="incDec" id="session-increment" onClick={props.seshIncrement}>+</p>
            <p id="session-length">{props.seshTime}</p>
            <p class="incDec" id="session-decrement" onClick={props.seshDecrement}>-</p>

        </div>
    )
}

export default Sesh