import React from 'react'

function Break(props) {
    return (
    <div className="flex-third">

        <h3 id="break-label">Break Length</h3>
        <p class="incDec" id="break-increment" onClick={props.breakIncrement}>+</p>
        <p id="break-length">{props.breakTime}</p>
        <p class="incDec" id="break-decrement" onClick={props.breakDecrement}>-</p>
        
    </div>
    )
}

export default Break