import React, { Component } from "react";
import logo from "./logo.svg";
import beep from "./beep.mp3";
import "./App.css";
import Pomodoro from "./Pomodoro";

class App extends Component {
  constructor() {
    super();
    this.state = {
      breakTime: 5,
      seshTime: 25,
      minutes: 25,
      seconds: "00",
      getTime: () => {
        this.setState(prevState => {
          if (prevState.minutes === "00" && prevState.seconds === "00") {
            document.getElementById("beep").play();
          }
          if (
            prevState.onBreak === false &&
            prevState.minutes === "00" &&
            prevState.seconds === "00"
          ) {
            return {
              minutes:
                prevState.breakTime < 10 && prevState.breakTime >= 1
                  ? "0" + prevState.breakTime
                  : prevState.breakTime,
              seconds: "00",
              onBreak: true,
              timerLabel: "Break"
            };
          } else if (prevState.onBreak === false) {
            return {
              timerLabel: "Session",
              minutes:
                prevState.minutes === 25 &&
                prevState.seshTime === 25 &&
                prevState.seconds === "00"
                  ? 24
                  : prevState.minutes === 25 &&
                    prevState.seshTime - 1 < 10 &&
                    prevState.seshTime - 1 >= 1 &&
                    prevState.seconds === "00"
                  ? "0" + (prevState.seshTime - 1)
                  : Number(prevState.seshTime) === 1 &&
                    prevState.seconds === "00"
                  ? "00"
                  : prevState.minutes === 25 && prevState.seconds === "00"
                  ? prevState.seshTime - 1
                  : prevState.minutes - 1 < 10 &&
                    prevState.minutes - 1 >= 1 &&
                    prevState.seconds === "00"
                  ? "0" + (prevState.minutes - 1)
                  : prevState.minutes > 10 &&
                    prevState.seconds === "00"
                  ? prevState.minutes - 1
                  : Number(prevState.minutes) === 1 &&
                    prevState.seconds === "00"
                  ? "00"
                  : prevState.minutes === "00"
                  ? "00"
                  : prevState.minutes,
              seconds:
                prevState.seconds === "00" && prevState.minutes !== "00"
                  ? 59
                  : prevState.minutes === "00" && prevState.seconds === "00"
                  ? "00" //Stop the timer when it hits "00:00"
                  : prevState.minutes - 1 === 0 &&
                    Number(prevState.seconds) - 1 === 0
                  ? "00" //If minutes will turn 0 AND seconds will turn 0, make seconds "00"
                  : Number(prevState.seconds - 1) < 10
                  ? "0" + Number(prevState.seconds - 1) //If seconds will become single digits, prepend "0"
                  : Number(prevState.seconds) - 1, //Otherwise decrement seconds by 1
              onBreak:
                prevState.minutes === "00" && prevState.seconds === "00"
                  ? true
                  : prevState.onBreak
            };
          } else if (
            prevState.onBreak === true &&
            prevState.minutes === "00" &&
            prevState.seconds === "00"
          ) {
            return {
              minutes:
                prevState.seshTime < 10 && prevState.seshTime >= 1
                  ? "0" + prevState.seshTime
                  : prevState.seshTime,
              seconds: "00",
              onBreak: false,
              timerLabel: "Session"
            };
          } else if (prevState.onBreak === true) {
            return {
              timerLabel: "Break",
              minutes:
                prevState.minutes === "05" &&
                prevState.breakTime === 5 &&
                prevState.seconds === "00"
                  ? "04"
                  : prevState.minutes === "05" &&
                    prevState.breakTime - 1 < 10 &&
                    prevState.breakTime - 1 >= 1 &&
                    prevState.seconds === "00"
                  ? "0" + (prevState.breakTime - 1)
                  : prevState.breakTime === 1 && prevState.seconds === "00"
                  ? "00"
                  : prevState.minutes === "05" && prevState.seconds === "00"
                  ? prevState.breakTime - 1
                  : Number(prevState.minutes) - 1 < 10 &&
                    Number(prevState.minutes) - 1 >= 1 &&
                    prevState.seconds === "00"
                  ? "0" + Number(prevState.minutes - 1)
                  : prevState.minutes > 10 &&
                    prevState.seconds === "00"
                  ? prevState.minutes - 1
                  : Number(prevState.minutes) === 1 &&
                    prevState.seconds === "00"
                  ? "00"
                  : prevState.minutes === "00" &&
                    prevState.seconds === "00" &&
                    prevState.seshTime - 1 < 10 &&
                    prevState.seshTime - 1 >= 1
                  ? "0" + prevState.seshTime
                  : prevState.minutes === "00" &&
                    prevState.seconds === "00" &&
                    prevState.seshTime > 9
                  ? prevState.seshTime
                  : prevState.minutes === "00"
                  ? "00"
                  : prevState.minutes,
              seconds:
                prevState.seconds === "00" && prevState.minutes !== "00"
                  ? 59
                  : prevState.minutes === "00" && prevState.seconds === "00"
                  ? "00" //Stop the timer when it hits "00:00"
                  : prevState.minutes - 1 === 0 &&
                    Number(prevState.seconds) - 1 === 0
                  ? "00" //If minutes will turn 0 AND seconds will turn 0, make seconds "00"
                  : Number(prevState.seconds - 1) < 10
                  ? "0" + Number(prevState.seconds - 1) //If seconds will become single digits, prepend "0"
                  : Number(prevState.seconds) - 1, //Otherwise decrement seconds by 1
              onBreak:
                prevState.seconds === "00" && prevState.minutes === "00"
                  ? false
                  : prevState.onBreak
            };
          }
        });
      },
      counting: false,
      onBreak: false,
      timerLabel: "Session",
      beep: beep
    };
    this.handleStart = this.handleStart.bind(this);
    this.handleStop = this.handleStop.bind(this);
    this.handleReset = this.handleReset.bind(this);
    this.handleBreakIncrement = this.handleBreakIncrement.bind(this);
    this.handleBreakDecrement = this.handleBreakDecrement.bind(this);
    this.handleSeshIncrement = this.handleSeshIncrement.bind(this);
    this.handleSeshDecrement = this.handleSeshDecrement.bind(this);
  }
  handleStart() {
    this.setState({ counting: true });
    this.timer = setInterval(this.state.getTime, 1000);
  }
  handleStop() {
    this.setState({ counting: false });
    clearInterval(this.timer);
  }
  handleReset() {
    clearInterval(this.timer);
    document.getElementById("beep").pause()
    document.getElementById("beep").currentTime = 0
    this.setState({
      seshTime: 25,
      breakTime: 5,
      minutes: 25,
      seconds: "00",
      timerLabel: "Session",
      onBreak: false,
      counting: false
    });
  }
  handleBreakIncrement() {
    this.setState(prevState => {
      return {
        breakTime: prevState.breakTime === 60 ? 60 : prevState.breakTime + 1
      };
    });
  }
  handleBreakDecrement() {
    this.setState(prevState => {
      return {
        breakTime: prevState.breakTime === 1 ? 1 : prevState.breakTime - 1
      };
    });
  }
  handleSeshIncrement() {
    this.setState(prevState => {
      return {
        seshTime: prevState.seshTime === 60 ? 60 : prevState.seshTime + 1
      };
    });
  }
  handleSeshDecrement() {
    this.setState(prevState => {
      return {
        seshTime: prevState.seshTime === 1 ? 1 : prevState.seshTime - 1
      };
    });
  }
  render() {
    return (
      <Pomodoro
        logo={logo}
        beep={this.state.beep}
        counting={this.state.counting}
        onBreak={this.state.onBreak}
        timerLabel={this.state.timerLabel}
        handleStart={this.handleStart}
        handleStop={this.handleStop}
        handleReset={this.handleReset}
        breakIncrement={this.handleBreakIncrement}
        breakTime={this.state.breakTime}
        breakDecrement={this.handleBreakDecrement}
        seshIncrement={this.handleSeshIncrement}
        seshTime={this.state.seshTime}
        seshDecrement={this.handleSeshDecrement}
        minutes={this.state.minutes}
        seconds={this.state.seconds}
      />
    );
  }
}

export default App;