import React from "react";

function Timer(props) {
  return (
    <div className="flex-third" id="timer">
      <h3 id="timer-label">{props.timerLabel}</h3>

      <div id="time-left">
        {props.minutes}:{props.seconds}
      </div>

      <div className="timer-buttons">
        <p
          id="start_stop"
          onClick={props.counting ? props.handleStop : props.handleStart}
        >
          Start/Stop
        </p>
        <p id="reset" onClick={props.handleReset}>
          Reset
        </p>
        <audio id="beep" src={props.beep} type="audio/mp3" />
      </div>
    </div>
  );
}

export default Timer;